@mixin onTablet {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin contentPaddingInLine {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container {
  @include contentPaddingInLine;
}

@mixin hover ($property, $toValue ) {
  transition: #{$property} 0.3s;

  &:hover {
    #{$property}: $toValue;
  }
}

@mixin pageGrid {
    --columns: 2;
    display: grid;
    row-gap: 24px;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);

    @include onTablet {
      --columns: 6;
      column-gap: 24px;
    }

    @include onDesktop {
      --columns: 12;
    }
}
